import React from 'react';
import {Container} from "react-bootstrap";
import {Link} from "react-router-dom";

const Footer = () => {
    return (
        <Container>
            <footer className="d-flex flex-wrap justify-content-between align-items-center py-3 my-4 border-top">
                <p className="col-md-4 mb-0 text-body-secondary">© 2023 Nedfid Developer</p>

                <a href="https://github.com/n3df1d" target="_blank" className="col-md-4 d-flex align-items-center justify-content-center mb-3 mb-md-0 me-md-auto link-body-emphasis text-decoration-none">
                    <img width="60" height="60" className="rounded-pill border" src="https://avatars.githubusercontent.com/u/121909787?v=4" alt="Nedfid Developer Logo"/>
                </a>

                <ul className="nav col-md-4 justify-content-end">
                    <li className="nav-item"><Link to="/" className="nav-link px-2 text-body-secondary">Главная</Link></li>
                    <li className="nav-item"><a href="#" className="nav-link px-2 text-body-secondary">Услуги</a></li>
                    <li className="nav-item"><Link to="/who-i" className="nav-link px-2 text-body-secondary">Кто я</Link></li>
                    <li className="nav-item"><a href="#" className="nav-link px-2 text-body-secondary">Контакты</a></li>
                </ul>
            </footer>
        </Container>
    );
};

export default Footer;