import React, {useState, useEffect} from 'react';
import {Container, Nav, Navbar} from "react-bootstrap";
import {LinkContainer} from 'react-router-bootstrap'
import style from './Header.module.scss'

const Header = () => {
    const [scrollNavbar, setScrollNavbar] = useState(0)

    useEffect(() => {
        window.addEventListener('scroll', () => setScrollNavbar(window.scrollY))
    })

    return (
        <Navbar className={(scrollNavbar < 50 ? style.navBarStyle0 : style.navBarStyle)+' '+style.NBS} fixed="top" expand="lg">
            <Container>
                <LinkContainer to="/">
                    <Navbar.Brand>Nedfid Developer</Navbar.Brand>
                </LinkContainer>
                <Navbar.Toggle aria-controls="basic-navbar-nav" />
                <Navbar.Collapse id="basic-navbar-nav">
                    <Nav className={"me-auto"}>
                        <LinkContainer to="/">
                            <Nav.Link>Главная</Nav.Link>
                        </LinkContainer>
                        <Nav.Link>Услуги</Nav.Link>
                        <LinkContainer to="/who-i">
                            <Nav.Link>Кто я</Nav.Link>
                        </LinkContainer>
                        <Nav.Link>Контакты</Nav.Link>
                    </Nav>
                    <Nav>
                        <Nav.Link href="https://gitlab.com/n3df1d" target="_blank">Kwork</Nav.Link>
                        <Nav.Link href="https://gitlab.com/n3df1d" target="_blank">Telegram</Nav.Link>
                        <Nav.Link href="https://gitlab.com/n3df1d" target="_blank">Whatsapp</Nav.Link>
                    </Nav>
                </Navbar.Collapse>
            </Container>
        </Navbar>
    );
};

export default Header;